import { Value } from '@ali/4ever-cangjie';

export default (value: Value) => {
  const { decorations } = value;
  const mentionSuggestion = decorations.find(
    decoration => decoration.mark.type === 'mention'
  );
  if (!mentionSuggestion) {
    return false;
  }

  // TODO: 等新版 slate
  //   selection.isCollapsed && selection.start.isInRange(mentionSuggestion);
  return true;
};
